import React from 'react';
import { Container, Table } from 'react-bootstrap';
import WorkList from './../../JSON/WorkList';
import './Homework.css';

const Homework = () =>
{
    const works = WorkList.map(work => 
    {
        var color;
        var date, month;
        const today = new Date();
        const year = today.getFullYear() + 543;
        
        if (today.getDate() < 10)
        {
            date = "0" + today.getDate();
        }
        else
        {
            date = today.getDate();
        }
        
        if (today.getMonth() + 1 < 10)
        {
            month = "0" + (today.getMonth() + 1);
        }
        else
        {
            month = today.getMonth() + 1;
        }
        
        const currentDate = date + "/" + month + "/" + year;

        for (var i = 0; i < WorkList.length; i++)
        {
            var fullDueDate = work.date;
            var dueDate = fullDueDate.charAt(0) + fullDueDate.charAt(1);
            var dueMonth = fullDueDate.charAt(3) + fullDueDate.charAt(4);
            var dueYear = fullDueDate.charAt(6) + fullDueDate.charAt(7) + fullDueDate.charAt(8) + fullDueDate.charAt(9);
            if (fullDueDate === currentDate)
            {
                color = 'table-warning';
            }
            else if ((dueMonth === month && date < dueDate) || (month < dueMonth && year <= dueYear))
            {
                color = 'table-success';
            }
            else
            {
                color = 'table-danger';
            }
        }

        return (
            <tr className={color}>
                <td>{work.work}</td>
                <td>{work.subject}</td>
                <td>{work.date}</td>
            </tr>
        );
    });

    return (
        <>
            <Container className='text-center'>
                <h1></h1><br/>
                <Table responsive striped bordered>
                    <thead>
                        <tr className='table-secondary'>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                        {works}
                    </thead>
                </Table>
            </Container>
        </>
    );
};

export default Homework;