import React from 'react';
import { Container, Navbar, Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import './Header.css';

const Header = () =>
{
    return (
        <>
            <Navbar expand='md' variant='dark' fixed='top' className='header'>
                <Container>
                    <LinkContainer to='/'>
                        <Navbar.Brand className='logo'>415 AI Official</Navbar.Brand>
                    </LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className='me-auto'>
                            <LinkContainer to='/'>
                                <Nav.Link className='link'></Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/student'>
                                <Nav.Link className='link'></Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/homework'>
                                <Nav.Link className='link'></Nav.Link>
                            </LinkContainer>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <div className='empty'/>
        </>
    );
}

export default Header;