import React from 'react';
import { Container, Table } from 'react-bootstrap';
import Schedule from './../../JSON/Schedule.json';

const Home = () =>
{
    const schedule = Schedule.map(subj =>
        {
            if (subj.day === "mon")
            {
                return (
                    <tr>
                        <td></td>
                        <td>{subj.p1}</td>
                        <td>{subj.p2}</td>
                        <td>{subj.p3}</td>
                        <td rowSpan={5}>{subj.p4}</td>
                        <td colSpan={2}>{subj.p5}</td>
                        <td>{subj.p7}</td>
                        <td>{subj.p8}</td>
                    </tr>
                );
            }
            else if (subj.day === "tue")
            {
                return (
                    <tr>
                        <td></td>
                        <td colSpan={2}>{subj.p1}</td>
                        <td>{subj.p3}</td>
                        <td>{subj.p5}</td>
                        <td>{subj.p6}</td>
                        <td>{subj.p7}</td>
                        <td>{subj.p8}</td>
                    </tr>
                );
            }
            else if (subj.day === "wed")
            {
                return (
                    <tr>
                        <td></td>
                        <td colSpan={2}>{subj.p1}</td>
                        <td>{subj.p3}</td>
                        <td>{subj.p5}</td>
                        <td>{subj.p6}</td>
                        <td>{subj.p7}</td>
                        <td>{subj.p8}</td>
                    </tr>
                );
            }
            else if (subj.day === "thu")
            {
                return (
                    <tr>
                        <td></td>
                        <td colSpan={2}>{subj.p1}</td>
                        <td>{subj.p3}</td>
                        <td>{subj.p5}</td>
                        <td>{subj.p6}</td>
                        <td>{subj.p7}</td>
                        <td>{subj.p8}</td>
                    </tr>
                );
            }
            else if (subj.day === "fri")
            {
                return (
                    <tr>
                        <td></td>
                        <td colSpan={2}>{subj.p1}</td>
                        <td>{subj.p3}</td>
                        <td>{subj.p5}</td>
                        <td>{subj.p6}</td>
                        <td>{subj.p7}</td>
                        <td>{subj.p8}</td>
                    </tr>
                );
            }
            return (
                <tr></tr>
            );
        });

    return (
        <>
            <Container className='text-center contain'>
                <h1> 415 AI Website</h1><br/><br/>
                <h1> 4/15 (2/2564)</h1>
                <Table responsive bordered>
                    <thead className='align-middle'>
                        <tr className='table-secondary'>
                            <th></th>
                            <th> 1</th>
                            <th> 2</th>
                            <th> 3</th>
                            <th> 4</th>
                            <th> 5</th>
                            <th> 6</th>
                            <th> 7</th>
                            <th> 8</th>
                        </tr>
                        <tr className='table-active'>
                            <th></th>
                            <th>08:20 - 09:15</th>
                            <th>09:15 - 10:10</th>
                            <th>10:10 - 11:00</th>
                            <th>11:00 - 12:00</th>
                            <th>12:00 - 12:55</th>
                            <th>12:55 - 13:50</th>
                            <th>13:50 - 14:45</th>
                            <th>14:45 - 15:40</th>
                        </tr>
                        {schedule}
                    </thead>
                </Table>
            </Container>
        </>
    );
}

export default Home;